.input{
	text-align: left;
	margin: 1em auto 0em;
	display: inline-block;

	label{
		font-weight: 700;
		font-size: 0.8em;
		width: 300px;
		display: inline-block;
		margin-bottom: 0.2em;
	}

	input[type='input']{
		-moz-appearance: textfield;
		-webkit-appearance: none;

		border-width: 1px;
		padding: 0.8em 1em;
		line-height: 1.5em;
		border-style: solid;
		border-color: #1191fc;
		border-radius: 0.2em 0em 0em 0.2em;
		outline: none;
		width: 300px;
		box-sizing: border-box;
		font-size: 0.9em;

		@media(max-width: 480px){
			width: 180px;
			// line-height: 1.15em;
			vertical-align: middle;
		}
	}

	.drop{
		display: inline-block;
		font-size: 0.9em;
		color: white;
		vertical-align: top;
		user-select: none;
		text-align: center;

		.top{
			.selected{
				width: 5.5em;
				border-width: 1px 0px;
				padding: 0.8em 0em;
				line-height: 1.5em;
				border-style: solid;
				border-color: #1191fc;
				background: #1191fc;
				display: inline-block;
				vertical-align: middle;
			}

			.icon{
				width: 2em;
				// font-size: 1em;
				border-width: 1px 0px;
				padding: 0.8em 0em;
				line-height: 1.5em;
				background: #0183f2;
				display: inline-block;
				vertical-align: middle;
				overflow: hidden;
				border-color: #0183f2;
				border-style: solid;
				border-radius: 0em 0.2em 0.2em 0em;

				@media(max-width: 480px){
					// font-size: 1em;
					// border-width: 1px;
					// padding: calc(0.95em - 1px) 0px;
				}
			}
		}

		.options{
			width: 7.5em;
			display: none;
			position: absolute;
			border-radius: 0em 0em 0.2em 0.2em;
			box-shadow: 0px 0px 3px -1px black;

			span{
				display: block;
				width: 50px;
				background: #0183f2;
				padding: 0.94em;
			}
		}

		&.open{
			.top{
				.selected{
					color: #1191fc;
					background: #ffffff;
					display: inline-block;
					vertical-align: middle;
				}

				.icon{
					border-radius: 0em 0.2em 0em 0em;
				}
			}

			.options{
				background: #0183f2;
				display: block;
			}

		}
	}

	div{
		cursor: pointer;
		input[type='checkbox']{
			cursor: pointer;
			margin-top: 0.3em;
		}

		label.sub{
			cursor: pointer;
			margin-top: 0.2em;
			width: auto;
			font-weight: 400;
			vertical-align: text-bottom;
			font-size: 0.9em;
		}
	}
}